.game{position:relative}.cards{display:flex;font-size:5rem;flex-wrap:wrap;justify-content:center;margin-bottom:20px;padding-bottom:20px}

.card{width:200px;height:200px;display:flex;justify-content:center;align-items:center;margin:10px;background-color:#ffcf00;transition:all 0.3s;box-shadow:5px 5px 0 0 rgba(0,0,0,0.3)}.card:hover{transform:scale(1.1)}.card.green{background-color:#00916e}.card.red{background-color:#fa003f}

.game-over-container{margin:0 auto;text-align:center;margin-top:60px}.game-over-container .game-over-title{font-size:2rem}

.btn{background-color:#00916e;color:white;white-space:nowrap;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:15px 40px;font-size:1rem;font-weight:400;text-align:center;border:none;border-radius:2px;box-shadow:5px 5px 0 0 rgba(0,0,0,0.3);cursor:pointer;transition:all 0.3s;margin-bottom:50px}.btn:hover{transform:scale(1.05)}

.overlay{position:absolute;width:100%;height:100%;z-index:-10}.overlay.block-clicking{z-index:10}

.header-main{margin:20px auto;text-align:center}

.instructions-container{max-width:500px;margin:0 auto;text-align:left}.instructions-container .instructions-title{text-align:center;text-decoration:underline;font-size:1.2rem}.instructions-container .instructions-list{counter-reset:list-number;margin-bottom:40px}.instructions-container .instructions-list li{position:relative;margin-top:25px;margin-bottom:25px;margin-left:10px;line-height:1.5rem}.instructions-container .instructions-list li:before{position:absolute;counter-increment:list-number;content:counter(list-number);display:flex;justify-content:center;align-items:center;margin-left:10px;left:-60px;width:35px;height:35px;font-size:1rem;background-color:#fa003f;border-radius:50%;color:#fff;font-weight:700}

.scoreboard{display:flex;justify-content:space-between;margin:0 auto;margin-bottom:20px;font-size:1.1rem;max-width:880px}.scoreboard .current-score-container,.scoreboard .best-score-container{margin:20px}.scoreboard .current-score-title,.scoreboard .best-score-title{white-space:nowrap}@media only screen and (max-width: 350px){.scoreboard{flex-direction:column;align-items:center;justify-content:center;padding:10px}.scoreboard .current-score-container,.scoreboard .best-score-container{margin:10px}}

