@import "../01-config/variables";

html {
    min-height: 100vh;
    background-color: $linen;
}

.l-container-1080 {
    max-width: 1080px;
    margin: 0 auto;
}

.l-container-full-screen {
    width: 100%;
}

.bg-green {
    background-color: #bfd8c8;
}

@media only screen and (max-width: 1200px) {
    .l-container-1080 {
        width: 90%;
    }
}
